const EventDetails = (props) => {
    console.log(props)
    const [displayBio, setdisplayBio] = useState(false);
    //displayBio is value, setdisplayBio is function, false is default value
    const [event, setEvent] = useState(null);


    useEffect(() => {
        const id = props.match.params.id
        console.log(id, parseInt(id)); //parseInt is convert id to integer
        let thisEvent = sampleData.find(e => e.id === parseInt(id)) //e.id === id is different data type, so need parseInt
        setEvent(thisEvent)
        console.log(thisEvent);
    }, []); //first action after return 


    return (
        <>
            <div className='mobile-header'>
                <Link to="/">
                    <IconContext.Provider value={{ size: "2em" }}>
                        <div className='arrow-back'><IoArrowBackCircleOutline /></div>
                    </IconContext.Provider>
                </Link>
            </div>
            {event && <div className='event-details'>
                <div className='event-details-thumbnail'><img src={logo} /></div>
                <div className='event-details-wrapper'>
                    <div className='event-details-title'>{event.title}</div>
                    {displayBio ? (<div className='event-details-desc'>{event.desc}
                        <br /><a className='event-details-link' onClick={() => setdisplayBio(false)}> View Less </a></div>
                    ) : (
                        <a className='event-details-link' onClick={() => setdisplayBio(true)}> Read More </a>)}
                    <IconContext.Provider value={{ size: "2em" }}>
                        <div className='event-details-button'>
                            <IoMdShareAlt />
                        </div>
                    </IconContext.Provider>
                </div>
                <div className='event-details-cards'>
                    <div className='item'>
                        <div className='title'>Event Details</div>
                        <div className='icon'><img src={calendar} /></div>
                        <div className='info'>{event.startDate}<br />{event.eventRange}</div>
                    </div>
                    <div className='item'>
                        <div className='icon'><img src={location} /></div>
                        <div className='info'>Hosted virtually via Zoom</div>
                    </div>
                    <div className='item'>
                        <div className='icon'><img src={ticket} /></div>
                        <div className='warning'>Only {event.ticketLeft} tickets left</div>
                    </div>
                </div>
                <div className='event-media-list'>
                    <div className='title'>Media</div>
                    <div className='media-list'>
                        <div className='media-list-item'>
                            <div className='media-list-thumb'>
                                <img src={media} />
                                <IconContext.Provider value={{ size: "1em" }}>
                                    <div className='play-icon'>
                                        <BsPlayCircle />
                                    </div>
                                </IconContext.Provider>
                            </div>
                            <div className='media-list-item-title'>
                                {mediaData.title}
                            </div>
                        </div>
                    </div>
                </div>
            </div>}
        </>

    )
}


export default EventDetails;